<template>
  <v-app>
    <right-sidebar :menuItems="networksData" serverRoute="modbus"></right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '' : 'nubeGreen'}`">
          <breadcrumbs-direction :items="dirs" />
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
          <v-tab :key="2"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="deviceHeaders"
              :items="dataTable"
              class="elevation-1 text-left"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              :custom-filter="filterCriteria"
              sort-by="uuid"
              group-by="category"
              show-group-by
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Modbus Devices View</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="onClickRefresh"
                      >Refresh<v-icon x-small right>mdi-refresh</v-icon></v-btn
                    >
                    <v-btn
                      x-small
                      class="mr-1 btn-height-22 white--text"
                      color="error"
                      @click="clickDeleteMulti"
                      :disabled="selected.length === 0"
                      >Delete<v-icon x-small right>mdi-delete</v-icon></v-btn
                    >
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field
                    v-model="search"
                    @input="onSearchChange"
                    append-icon="mdi-magnify"
                    label="Search"
                    hide-details
                  ></v-text-field>
                </v-toolbar>
              </template>
              <template v-slot:[`group.header`]="{ group, headers, toggle, isOpen, items }">
                <td :colspan="headers.length">
                  <div class="row" style="align-items: center">
                    <v-btn @click="toggle" x-small icon :ref="group">
                      <v-icon>{{ isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right' }}</v-icon> </v-btn
                    >&nbsp;&nbsp;&nbsp;&nbsp;
                    <v-checkbox
                      v-bind:value="
                        selected.filter(d => d.network_uuid === items[0].network_uuid).length == items.length
                      "
                      @change="check($event, items)"
                    ></v-checkbox>
                    <router-link
                      :to="`/modbus/networks/${items[0].network_uuid}/devices`"
                      class="font-weight-bold text-style"
                    >
                      {{ group }}
                    </router-link>
                  </div>
                </td>
              </template>

              <template v-slot:[`item.actions`]="{ item }">
                <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
              </template>
              <template v-slot:[`item.enable`]="{ item }">
                <v-checkbox v-model="item.enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.supports_multiple_rw`]="{ item }">
                <v-checkbox v-model="item.supports_multiple_rw" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.address`]="{ item }">
                <v-edit-dialog :return-value.sync="item.address" @save="updateDevice(item)" large>
                  <b>{{ item.address }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.address"
                      type="number"
                      min="0"
                      :rules="[() => !isExistingvalue(item, 'address') || 'Address Existed']"
                      @input="val => (item.address = val ? Number(val) : 0)"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.ping_point`]="{ item }">
                <v-edit-dialog :return-value.sync="item.ping_point" @save="updateDevice(item)" large>
                  <b>{{ item.ping_point }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.ping_point" placeholder="(Func:Addr:Len 1:1:1)"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:[`header.enable`]>
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:[`header.supports_multiple_rw`]>
                {{ toUpperTextKey('supports_multiple_rw') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('supports_multiple_rw')" color="primary">mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.ping_point`]>
                {{ toUpperTextKey('ping_point') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('ping_point')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
          <v-tab-item :key="2">
            <system-status-table server="modbus"></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!--Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Device</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    @input="changeName"
                    v-model="editedItem.name"
                    label="device name"
                    required
                    :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed', rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.supports_multiple_rw" label="supports multiple r/w"></v-checkbox>
                </v-col>
                <!-- <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_ip" label="IP" :rules="[() => !isExistingvalue(editedItem, 'tcp_ip') || 'IP Existed']"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_port" label="Port" type="number"></v-text-field>
                </v-col> -->
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.zero_based" label="zero mode"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                  <v-text-field v-model="editedItem.ping_point" label="ping point (Func:Addr:Len 1:1:1)"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="editedItem.address"
                    label="address"
                    type="number"
                    :rules="[() => !isExistingvalue(editedItem, 'address') || 'Address Existed']"
                    @input="val => (editedItem.address = val ? Number(val) : 0)"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="updateDevice(editedItem)" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            min="0"
            type="number"
            v-model.number="editAll[editField]"
            :label="toUpperTextKey(editField)"
            @input="val => (editAll[editField] = val ? Number(val) : 0)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'string'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import * as modbusServerService from '../../services/modbusServerService';
import { RULES, TOPIC, DEVICE_HEADERS, DEVICE_EDIT_ALL_DEFAULT } from './modbusConstant';
import { toUpperTextKey, conventionNaming } from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    rules: { ...RULES },
    deviceHeaders: [...DEVICE_HEADERS],
    editAll: { ...DEVICE_EDIT_ALL_DEFAULT },
    tableProps: { ...DATA_TABLE_PROPS },
    dialog: false,
    dialogDelete: false,
    dialogMassEdit: false,
    tab: 0,
    action: '',
    valid: false,
    selected: [],
    editedItem: {},
    search: '',
    searchItems: [],
    editField: '',
    intervalId: null,
    devicesOfNW: [],
    dataTable: [],
    dirs: [
      { text: 'NETWORKS', link: '/modbus/networks', disabled: false },
      { text: 'DEVICES VIEW', disabled: true },
    ],
    networks: [],
    network: {},
    loadingData: false,
  }),
  computed: {
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.mbNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getAllDevices();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    this.$store.commit('showOverlay');

    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      this.$store.commit('hideOverlay');

      next();
    });
  },
  mounted() {
    this.getAllDevices();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getAllDevices();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllDevices() {
      this.loadingData = true;
      modbusServerService.fetchNetworks({ with_children: true }).then(data => {
        this.networks = data;
        this.getDataTable(data);
        this.loadingData = false;
      });
    },
    getDataTable(networks) {
      this.dataTable = [];
      networks.forEach(network => {
        if (network.devices.length > 0) {
          const devicesPreNW = network.devices.map(device => {
            return { ...device, category: network.name };
          });
          this.dataTable = this.dataTable.concat(devicesPreNW);
        }
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable.findIndex(d => d.uuid == item.uuid);
      this.devicesOfNW = this.dataTable.filter(device => device.network_uuid === item.network_uuid);
      this.editedItem = Object.assign({}, item);
      this.network = this.networks.find(n => n.uuid === item.network_uuid);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable.indexOf(this.editedItem);
      this.dataTable.splice(this.editedIndex, 1);
      await modbusServerService.removeDevice(this.editedItem.uuid);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(item, fieldName) {
      if (!this.devicesOfNW) return false;
      let value = item[fieldName];
      let devices = _cloneDeep(this.devicesOfNW);
      const index = devices.findIndex(d => d.uuid == item.uuid);
      if (index !== -1) {
        devices.splice(index, 1); //when edit
      }
      return devices.find(d => d[fieldName] === value) != null;
    },
    updateDevice(data) {
      modbusServerService.updateDevice(data.uuid, data).then(response => {
        const index = this.dataTable.findIndex(d => d.uuid == response.uuid);
        Object.assign(this.dataTable[index], response);
        this.close();
        this.$toasted.show('update success');
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'nubeYellow';
      } else {
        classColor = !item.enable ? 'nubeLightGrey' : '';
      }
      return classColor;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...DEVICE_EDIT_ALL_DEFAULT };
    },
    onClickRefresh() {
      this.getAllDevices();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if (!e) this.selected = [...this.selected].filter(point => point.network_uuid != items[0].network_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    changeName(val) {
      this.editedItem.name = conventionNaming(val, this.setting);
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
