import http from '../axios';
import qs from 'query-string';

export const endpoint = 'modbus-server';

export const pollPoint = data => {
  return http.post(`${endpoint}/poll-point`, data);
};

export const fetchNetworks = query => {
  let uri = `${endpoint}/network`;
  if (query) {
    uri = `${uri}?${qs.stringify(query)}`;
  }
  return http.get(`${endpoint}/network?${qs.stringify(query)}`);
};

export const deleteNetwork = id => {
  return http.delete(`${endpoint}/network/${id}`);
};

export const fetchNetwork = (id, query) => {
  let uri = `${endpoint}/network/${id}`;
  if (query) {
    uri = `${uri}?${qs.stringify(query)}`;
  }
  return http.get(uri);
};

export const fetchDevice = (id, query) => {
  let uri = `${endpoint}/device/${id}`;
  if (query) {
    uri = `${uri}?${qs.stringify(query)}`;
  }
  return http.get(uri);
};

export const updateNetwork = (id, data) => {
  return http.put(`${endpoint}/network/${id}`, data);
};

export const createNetwork = data => {
  return http.post(`${endpoint}/network`, data);
};

export const fetchAllDevices = () => {
  return http.get(`${endpoint}/all-devices`);
};

export const createDevice = data => {
  return http.post(`${endpoint}/device`, data);
};

export const updateDevice = (id, data) => {
  return http.put(`${endpoint}/device/${id}`, data);
};

export const removeDevice = id => {
  return http.delete(`${endpoint}/device/${id}`);
};

export const fetchPoints = () => {
  return http.get(`${endpoint}/all-points}`);
};

export const fetchPoint = id => {
  return http.get(`${endpoint}/point/${id}`);
};

export const updatePoint = (id, data) => {
  return http.put(`${endpoint}/point/${id}`, data);
};

export const patchPoint = (id, data) => {
  return http.patch(`${endpoint}/point/${id}`, data);
};

export const removePoint = id => {
  return http.delete(`${endpoint}/point/${id}`);
};

export const createPoint = data => {
  return http.post(`${endpoint}/point`, data);
};
